import http from "./http-common";


class ApplicantDataService {
  getAll() {
    return http.get("/applicant")
  }

  get(id) {
    return http.get(`/applicant/${id}`);
  }

  getComments(id) {
    return http.get(`/applicant/${id}/comment`);
  }

  leaveReview(id, data) {
    return http.put(`/applicant/${id}/review`, data)
  }

  delete(id) {
    return http.delete(`/applicant/${id}`);
  }
}

export default new ApplicantDataService();