import Vue from 'vue'
import Vuex from 'vuex'
import ApplicantDataService from '@/services/ApplicantDataService'
import AuthDataService from '@/services/AuthDataService'
import createPersistedState from "vuex-persistedstate"

Vue.use(Vuex)

export default new Vuex.Store({
  plugins: [createPersistedState()],
  state: {
    current_user: null,
    applicants: [],
    access_token: null
  },
  getters: {
    current_user: (state) => state.current_user,
    applicants: (state) => state.applicants,
    max_applicants: (state) => state.applicants.length,
    pending_applicants: (state) => state.applicants.filter(x => x.status === false),
    approved_applicants: (state) => state.applicants.filter(x => x.status === true),
    access_token: (state) => state.access_token
  },
  mutations: {
    SET_CURRENT_USER: (state, payload) => { state.current_user = payload },
    SET_APPLICANTS: (state, payload) => { state.applicants = payload },
    SET_ACCESS_TOKEN: (state, payload) => { state.access_token = payload }
  },
  actions: {
    loginUser({commit}, payload) {
      let { email, password } = payload
      return new Promise((resolve, reject) => {
        AuthDataService.login(email, password)
          .then(res => {
            commit('SET_CURRENT_USER', res.data.user)
            commit('SET_ACCESS_TOKEN', res.data.access_token)
            resolve()
          })
          .catch(err => reject(err))
      })
    },
    logoutUser({ commit }) {
      return new Promise((resolve) => {
        commit('SET_CURRENT_USER', null)
        commit('SET_ACCESS_TOKEN', null)
        commit('SET_APPLICANTS', null)
        resolve(true)
      })
    },
    loadApplicants({ commit }) {
      ApplicantDataService.getAll()
        .then((res) => {
          commit('SET_APPLICANTS', res.data)
        })
        .catch(err => console.error(err))
    },
  },
  modules: {
  }
})
